<template>
  <div class="signup" style = "width:80%; margin:auto; padding-top:30px;">
    <h2 style = "text-align : center; "></h2>
    <b-form @submit="postUser" @reset="onSubmit" v-if="show">
        <b-form-group id="ID-InputGroup" label="ID:" label-for="id-input">
           <v-text-field
            v-model="form.name"
            label=""
            single-line
            hide-details
          ></v-text-field>
        </b-form-group>

        <b-form-group id="password-InputGroup" label="Password:" label-for="pwd-input">
          <v-text-field
            type="password"
            v-model="form.password"
            label=""
            single-line
            hide-details
          ></v-text-field>
        </b-form-group>

      <b-form-group id="password-InputCheckGroup" label="Password check:" label-for="pwd-input-check">
        <v-text-field
            type="password"
            v-model="form.password_check"
            label=" "
            single-line
            hide-details
          ></v-text-field>
      </b-form-group>

        <b-form-group id="nickname-InputGroup" label="Nickname:" label-for="nickname-input">
          <v-text-field
            v-model="form.nickname"
            label=""
            single-line
            hide-details
          ></v-text-field>
        </b-form-group>

        <b-form-group id="email-InputGroup" label="Email:" label-for="email-input">
          <v-text-field
            v-model="form.email"
            label=" "
            single-line
            hide-details
          ></v-text-field>
        </b-form-group>
  
        <b-form-group id="major-InputGroup" label="Major:" label-for="majour-input">
            <b-form-select id="major-input"
                      :options="majors"
                      required
                      v-model="form.major"
                      >
        </b-form-select>
        </b-form-group>
        <b-form-group>
        <b-row align-h="center">
            <b-button type="submit" @click="$emit('close')" align-h="center" style = "background-color : #C6D6F7; border:#C6D6F7; font-weight: bold; margin-top:20px;"> </b-button>
        </b-row>
        </b-form-group>
    </b-form>
    </div>
</template>

<script>
export default {
  name: 'SignUp',
  data () {
    return {
      form: {
        email: '',
        name: '',
        nickname: '',
        password: '',
        password_check: '',
        major: null
      },
      majors: [
         { text: '', value: null },
      ],
      show: true
    }
  },
  created () {
    this.getMajor()
  },
  methods: {
     //   
    getMajor() {
      this.$http
        .get("/api/major/all")
        .then(res => {
          for (var i = 0; i < res.data.length; i++) {
            this.majors.push({text: res.data[i].major ,value: res.data[i].major});
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    onSubmit (evt) {
      evt.preventDefault()
      alert(JSON.stringify(this.form))
    },
    postUser (evt) {
      evt.preventDefault() // 
      if (this.form.password_check === this.form.password) {
        this.$http.post('/api/signup', { // 
          username: this.form.name,
          password: this.form.password,
          email: this.form.email,
          nickname: this.form.nickname,
          major: this.form.major
        })
          .then((res) => {
            const status = res.status
            console.log(status)
            // redirect logic
            if (status == '200') {
              alert('  .     .')
              this.$router.push('/')
            } else if (status == '202') {
              alert('  ID .')
            } else if (status == '204') {
              alert('    .')
            }
          }).catch((err) => {
            console.log(err)
          })
      } else {
        alert('   .')
      }
    }
  }
}
</script>
<style>
 #major-input{
   margin-top:20px;
 }
 .v--modal-overlay .v--modal-box {
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.705);
}
</style>


