<template>
  <b-card no-body>
    <b-tabs card>
      <b-tab title="" active>
        <div class="out_profile">
          <div class="in_profile">
        <b-card id="profile_card" bg-variant="light">
          <b-form @reset="onSubmit" v-if="show">
            <b-form-group horizontal
                          breakpoint="lg"
                          label=""
                          label-size="lg"
                          label-class="font-weight-bold pt-0"
                          class="mb-0">
            </b-form-group>
              <b-form-group horizontal
                            label="ID:"
                            label-class="text-sm-right"
                            label-for="id-input">
                <b-form-input id="id-input"
                              v-model="form.name"
                              required
                              disabled
                              placeholder=""
                ></b-form-input>
              </b-form-group>
              <b-form-group horizontal
                            label="Current Password:"
                            label-class="text-sm-right"
                            label-for="current-pwd-input">
                <b-form-input id="current-pwd-input"
                              type="password"
                              v-model="form.current_password"
                              required
                              :disabled="!status"
                              placeholder=" "></b-form-input>
              </b-form-group>
            <b-form-group horizontal
                          label="Password:"
                          label-class="text-sm-right"
                          label-for="pwd-input">
              <b-form-input id="pwd-input"
                            type="password"
                            v-model="form.password"
                            required
                            :disabled="!status"
                            placeholder=""></b-form-input>
            </b-form-group>
            <b-form-group horizontal
                          label="Password check:"
                          label-class="text-sm-right"
                          id="password-InputCheckGroup"
                          label-for="pwd-input-check">
              <b-form-input id="pwd-input-check"
                            type="password"
                            v-model="form.password_check"
                            required
                            :disabled="!status"
                            placeholder="">
              </b-form-input>
            </b-form-group>
            <b-form-group horizontal
                          label="Nickname:"
                          id="nickname-InputGroup"
                          label-class="text-sm-right"
                          label-for="nickname-input">
              <b-form-input id="nickname-input"
                            type="text"
                            v-model="form.nickname"
                            required
                            placeholder="">
              </b-form-input>
            </b-form-group>
            <b-form-group horizontal
                          label-class="text-sm-right"
                          id="email-InputGroup"
                          label="Email:"
                          label-for="email-input">
              <b-form-input id="email-input"
                            type="email"
                            v-model="form.email"
                            required
                            disabled
                            placeholder=" ">
              </b-form-input>
            </b-form-group>
            <b-form-group horizontal
                          label-class="text-sm-right"
                          id="major-InputGroup"
                          label="Major:"
                          label-for="major-input">
              <b-form-select id="major-input"
                             :options="majors"
                             required
                             v-model="form.major">
              </b-form-select>
            </b-form-group>
            <b-form-group>
              <b-form-checkbox id="checkbox1"
                               v-model="status">
                  !
              </b-form-checkbox>
            </b-form-group>
          </b-form>
          <b-row align-h="center">
            <b-button @click="updateUser" v-b-popover.hover="'      !'"
                      title=" " type="submit" variant="primary" align-h="center">
            </b-button>
          </b-row>
        </b-card>
          </div>
        </div>
      </b-tab>
      <b-tab title="  " @click="getMyEval()">
        <div class="myEvaluation">
          <div id="myEval_container">
            <h5 id="mynoresult" v-if="my_eval_subject.length == 0">  .</h5>
            <v-flex xs12>
              <v-container fluid>
                <v-layout row wrap>
                  <v-flex
                    id="evaluate" v-for="item in my_eval_subject" v-bind:key="item.id"
                    v-if="item.userId === form.name"
                    xs3
                  >
                    <v-hover>
                      <v-card
                        slot-scope="{ hover }"
                        :class="`elevation-${hover ? 12 : 2}`"
                        class="mx-auto"
                        width="345"
                        flat tile>
                        <div class="myEvalContainer">
                          <h3 style="padding-top:20px">Course</h3>
                          <h5>{{item.lecture}}</h5>
                          <h3>Professor</h3>
                          <h5>{{item.professor}}</h5>
                          <v-rating v-model="item.evaluation.totalGrade"
                                    color="yellow darken-3"
                                    background-color="grey darken-1"
                                    readonly=true>
                          </v-rating>
                          <h5 class="circle">{{parseFloat(item.evaluation.totalGrade).toFixed(1)}}</h5>
                          <h5>{{item.semester}}</h5><br>
                          <router-link :to="{name:'eval-view',params:{id: item.id}}">
                            <button type="submit" class="plusView" style="margin-bottom:15px;">Read More</button>
                          </router-link>
                        </div>
                      </v-card>
                    </v-hover>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-flex>
          </div>
        </div>
      </b-tab>
    </b-tabs>
  </b-card>
</template>

<script>
export default {
  name: 'Profile',
  data () {
    return {
      form: {
        email: '',
        name: '',
        nickname: '',
        current_password: '',
        password: '',
        password_check: '',
        major: null
      },
      majors: [
        {text: '', value: null},
        '', '', '', ''
      ],
      my_eval_subject: [],
      show: true,
      status: true
    }
  },
  methods: {
    getMyEval () {
      this.$http.get('/api/class/evaluation')
        .then((res) => {
          this.my_eval_subject = res.data
          console.log('')
          console.log(res.data)
        })
    },
    onSubmit (evt) {
      evt.preventDefault()
      alert(JSON.stringify(this.form))
    },
    updateUser (evt) {
      evt.preventDefault() // 
      if (this.status === false) {
        this.$http.put('/api/profile/user', {
          isChangePwd: false,
          username: this.form.username,
          email: this.form.email,
          nickname: this.form.nickname,
          major: this.form.major
        })
          .then(res => {
            const status = res.status
            if (status === 200) {
              alert('  .')
              this.$router.push('/main')
            } else if (status === 205) {
              alert('  .')
            }
          }).catch(e => {
            alert(e)
          })
      } else {
        if (this.form.password === this.form.password_check) {
          this.$http.put('/api/profile/user', {
            isChangePwd: true,
            username: this.form.username,
            current_password: this.form.current_password,
            password: this.form.password,
            email: this.form.email,
            nickname: this.form.nickname,
            major: this.form.major
          })
            .then(res => {
              const status = res.status
              if (status === 200) {
                alert('     .')
                this.$router.push('/main')
              } else if (status === 203) {
                alert('[Error]    .')
              } else if (status === 205) {
                alert('  .')
              }
            }).catch(e => {
              alert(e)
            })
        } else {
          alert('     .')
        }
      }
    }
  },
  beforeRouteUpdate (to, from, next) {
    this.$http.get('/api/profile/user')
      .then(res => {
        this.form.name = res.data.username
        this.form.password = res.data.password
        this.form.email = res.data.email
        this.form.nickname = res.data.nickname
        this.form.major = res.data.major
      })
    next()
  },
  created () {
    this.$EventBus.$emit('removeTab', true)
    this.$http.get('/api/profile/user')
      .then(res => {
        this.form.name = res.data.username
        this.form.email = res.data.email
        this.form.nickname = res.data.nickname
        this.form.major = res.data.major
      })
  }
}
</script>
<style>

  div.out_profile{
    text-align: center;
  }

  div.in_profile{
    display: inline-block;
  }

  #profile_card {
    width: 600px;
  }

  #mynoresult {
    margin-top: 120px;
    text-align: center;
  }

  .card-header {
    padding-top: 2vh;
    font-size: 18px;
    font-weight: bold;
  }

  .nav-tabs .nav-link {
    height: 7vh;
    padding-left: 3vw;
    padding-right: 3vw;
    margin-top: 3vh;
    margin-left: 5px;
  }

  .myEevalContainer {
    background-color: lightgray;
    margin-top: 60px;
    padding-bottom: 20px;
    width: 330px;
    height: 450px;
    background: white;
    text-align: center;
  }

  .myEvalContainer h3 {
    font-weight: bold;
  }

  #myEval_container {
    margin-top: 40px;
    text-align: center;
  }

  .circle {
    border: thick solid #bb252b;
    border-radius: 100%;
    padding-top: 5px;
    padding-bottom: 5px;
    width: 80px;
    font-size: 45px;
    margin-left: 38%;
    margin-right: 38%;
    margin-top: 25px;
    margin-bottom: 30px;
  }

  .plusView {
    font-size: 15px;
    margin-top: 10px;
    color: white;
    height: 40px;
    width: 90px;
    font-weight: bold;
    background: #165833;
    text-align: center;
    border: transparent;
    border-radius: 10px;
  }
</style>



